<template>
    <div>  
        <div class="loginNav">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">班干部设置</p>
            <span class="f26 cfff pr-30" style="position:absolute;top:0;right:0;line-height:0.84rem;z-index:99;" @click="$router.push({path:'/setCadres'})">管理</span>
        </div>
        <div class="content f28 c333" style="padding:0.84rem 0 1.7rem;">
            <ul class="pl-30">
                <li class="b_line pr-30" v-for="(item,index) in list" :key="index">
                    <img :src="$api.fileUrl + item.head_image">
                    <span>{{item.username}}</span>
                    <span class="onDuty f20 cfff ml-20">{{item.ucStation}}</span>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>

export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            list:[]
        }
    },
    methods : {
        returns(){
            this.$router.push({path:'/class'});//返回上一层
        },
        //获取所有学生（班干部）
         getStudent(){
            this.$toast.loading({
                mask: true,
                message: '加载中...',
                duration:0
            });
            this.$http({url:this.$api.class.getStudent},{classesId:JSON.parse(localStorage.getItem('zhxy_user')).classesId,type:1}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.list = data.data;
                    this.$toast.clear();
                }else{
                    this.$toast(data.msg);
                }
            },err =>{
                this.$toast(data.msg);
            })
        },
    },
    created() {
        this.getStudent();
    },
}
</script>

<style scoped>
     /* 修改组件样式 */
     .loginNav{
        position: fixed;
    }
    
    .content{
        line-height: 1rem;
    }
    .content img{
        width:.6rem;
        height:.6rem;
        border-radius: 50%;
        margin-right: .3rem;
    }
    li{
        display: flex;
        align-items: center;
    }
    .onDuty{
        /* width:.7rem; */
        padding: 0 0.05rem;
        height:.28rem;
        background:#7C88EE;
        line-height: .28rem;
        display: inline-block;
        text-align: center;
        border-radius: .05rem;
    }
</style>




